// export default httpClient;

const isProducut = false;
let BASE_URL = '';
let BASE_URL_FORM = '';
let BASE_URL_TICKET = '';

if (isProducut) {
  BASE_URL = 'https://erequest.kyta.fpt.com';
  BASE_URL_FORM = 'https://eform.kyta.fpt.com/preview-template/';
  BASE_URL_TICKET = 'https://erequest.kyta.fpt.com/p-ticket-detail/';
} else {
  BASE_URL = 'https://demo.erequest.kyta.fpt.com';
  BASE_URL_FORM = 'https://demo.eform.kyta.fpt.com/preview-template/';
  BASE_URL_TICKET = 'https://demo.erequest.kyta.fpt.com/p-ticket-detail/';
}

export {BASE_URL, BASE_URL_FORM, BASE_URL_TICKET};

interface IRequestFetch {
  endpoint: string;
  method: 'GET' | 'POST' | 'PUT' | 'PATCH' | 'DELETE';
  body?: any;
  headers?: any;
  params?: any;
}

export const fetchRequest = async ({
  endpoint = '',
  method,
  body,
  headers = {
    'Content-Type': 'application/json',
  },
}: IRequestFetch) => {
  try {
    const dataResponse = await fetch(BASE_URL + endpoint, {
      method: method,
      headers,
      body: JSON.stringify(body),
    });
    const data = await dataResponse.json();
    const status = dataResponse.status;

    return Promise.resolve({
      data,
      status,
    });
  } catch (error) {
    return Promise.reject(error);
  }
};

export default fetchRequest;
