import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../../../components/Header/HeaderCM';
import TextFieldCM from '../../../../components/TextField';
import {SizeDP} from '../../../../../constants/Size';
import styles from './styles';
import {
  IcSearch,
  IconFilter,
  IconLeftArrow,
} from '../../../../../assets/images';
import BottomSheetCM from '../../../../components/BottomSheet';
import {useCallback, useEffect, useState} from 'react';
import {FlatList, Keyboard, TouchableWithoutFeedback, View} from 'react-native';
import TextCM from '../../../../components/Text';
import ModalPicDateCM from '../../../../components/ModalPicDate';
import DropdownListCM, {IDropDown} from '../../../../components/DropDownList';
import {dataPrioritySelect, dataStatus} from './helper';
import {ListServices} from '../../../../../services';
import {CacheUtils} from '../../../../../utils';
import {
  ISearch,
  IServices,
  ITicket,
} from '../../../../../model/interface/service.interface';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {isEmpty} from 'lodash';
import ButtonCM from '../../../../components/Button';
import GroupButtonCM from '../../../../components/GroupButton';
import {useAppSelector} from '../../../../../hooks/useRedux';
import CardCM from '../../../../components/Card';

const AddTicketRelative = ({route}: any) => {
  const {onGoBack, paramsSelectTicket} = route?.params;
  const navigation = useNavigation();
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const [openModalDateFrom, setOpenModalDateFrom] = useState<boolean>(false);
  const [openModalDateTo, setOpenModalDateTo] = useState<boolean>(false);
  const [groupService, setGroupService] = useState<IDropDown>();
  const [groupStatusTicket, setGroupStatusTicket] = useState<IDropDown[]>([]);
  const [groupServices, setGroupServices] = useState<IDropDown[]>([]);
  const [priority, setPriority] = useState<IDropDown>();
  const [dateFrom, setDateFrom] = useState<Date>();
  const [dateTo, setDateTo] = useState<Date>();
  const [totalRecoders, setTotalRecoders] = useState<number>(0);
  const [listTicket, setListTicket] = useState<ITicket[]>([]);
  const [listTicketSelect, setListTicketSelect] =
    useState<ITicket[]>(paramsSelectTicket);
  const [page, setPage] = useState<number>(0);
  const [reloading, setReloading] = useState<boolean>(false);

  const {dataAccount} = useAppSelector(state => state.account);

  useEffect(() => {
    (async () => {
      await getListTicketRelative(page);
    })();
  }, [page]);

  useFocusEffect(
    useCallback(() => {
      (async () => {
        setPage(0);
        await getGroupService();
      })();
    }, []),
  );

  const handleSubmit = async () => {
    setPage(0);
    await getListTicketRelative(0);
  };

  const getListTicketRelative = async (page: number) => {
    try {
      const bodySubmit: ISearch = {
        createdBy: dataAccount?.email ?? 'elmstest7@gmail.com', //
        myTicket: true,
        textSearch: '',
        ticketStatus: groupStatusTicket as any,
        service: groupService?.value ? [groupService?.value] : [],
        priority: priority?.value ? [priority?.value] : [],
        startDate: dateFrom?.toISOString() ? dateFrom?.toISOString() : null,
        endDate: dateTo?.toISOString() ? dateTo?.toISOString() : null,
      };

      const token = (await CacheUtils.getToken()) ?? '';
      const data = await ListServices.getTicketList(
        page,
        20,
        'createdDate,desc',
        token,
        bodySubmit,
      );
      if (page === 0) {
        setListTicket(data?.content);
      } else {
        setListTicket([...listTicket, ...data?.content]);
      }
      setTotalRecoders(data?.page?.totalRecords);
    } catch (error) {
    } finally {
      setReloading(false);
      setIsVisible(false);
    }
  };

  const getGroupService = async () => {
    try {
      const token = (await CacheUtils.getToken()) ?? '';
      const data = await ListServices.getServiceMenu(
        0,
        10000,
        undefined,
        token,
        true,
      );
      const filterData = data.content.map((item: IServices) => {
        return {
          label: item.serviceName,
          value: item.id,
        };
      });
      setGroupServices(filterData);
    } catch (error) {}
  };

  const renderListFilter = () => {
    return (
      <>
        {!isEmpty(groupServices) && (
          <View>
            <TextCM>Nhóm dịch vụ</TextCM>
            <DropdownListCM
              onSelect={setGroupService}
              valueSelect={groupService}
              dropdownPosition="auto"
              placeholder="Chọn nhóm dịch vụ"
              maxHeight={SizeDP(200)}
              listData={groupServices}
            />

            <TextCM>Mức độ ưu tiên</TextCM>
            <DropdownListCM
              onSelect={setPriority}
              valueSelect={priority}
              dropdownPosition="auto"
              placeholder="Chọn mức độ ưu tiên"
              listData={dataPrioritySelect}
              maxHeight={SizeDP(200)}
            />

            <TextCM>Trạng thái ticket</TextCM>
            <DropdownListCM
              onSelectMui={setGroupStatusTicket}
              valueMuiSelect={groupStatusTicket}
              dropdownPosition="top"
              placeholder="-Chọn-"
              multiple={true}
              listData={dataStatus}
            />

            <TextCM style={{paddingVertical: SizeDP(10)}}>
              Lọc theo ngày tạo
            </TextCM>
            <View
              style={{
                flexDirection: 'row',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}>
              <ModalPicDateCM
                title="Từ ngày"
                initDate={dateFrom}
                onClose={() => setOpenModalDateFrom(false)}
                onOpen={() => setOpenModalDateFrom(true)}
                onSave={date => setDateFrom(date)}
                isVisible={openModalDateFrom}
              />

              <View style={{transform: [{rotate: '180deg'}]}}>
                <IconLeftArrow />
              </View>

              <ModalPicDateCM
                title="Đến ngày"
                initDate={dateTo}
                onClose={() => setOpenModalDateTo(false)}
                onOpen={() => setOpenModalDateTo(true)}
                isVisible={openModalDateTo}
                onSave={date => setDateTo(date)}
              />
            </View>

            <ButtonCM
              label="Lưu"
              style={{marginTop: SizeDP(20)}}
              onPress={handleSubmit}
            />
          </View>
        )}
      </>
    );
  };

  const fetchNextPage = () => {
    if (listTicket.length < totalRecoders) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setReloading(true);
    }
    setPage(0);
  };

  const handleBackCreateTicket = () => {
    onGoBack(listTicketSelect);
    navigation.goBack();
  };

  return (
    <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
      <>
        <HeaderCM title="Thêm ticket liên quan" />
        <SafeAreaView
          style={styles.ctnArea}
          edges={['bottom', 'left', 'right']}>
          <TextFieldCM
            viewIconLeft={<IcSearch />}
            viewIconRight={<IconFilter />}
            onIconPress={() => setIsVisible(true)}
            label="Tìm kiếm theo tên ticket, người tạo..."
          />
          <FlatList
            data={listTicket}
            refreshing={reloading}
            onRefresh={onRefresh}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            contentContainerStyle={{paddingRight: SizeDP(2)}}
            onEndReached={fetchNextPage}
            onEndReachedThreshold={0.5}
            keyExtractor={item => '' + item.id}
            renderItem={({item}) => (
              <CardCM
                dataCard={item}
                onSelect={setListTicketSelect}
                select={listTicketSelect}
                account={dataAccount}
              />
            )}
          />

          <GroupButtonCM
            labelLeft="Hủy"
            labelRight={`Chọn (${listTicketSelect.length}) ticket`}
            onPressLeft={() => navigation.goBack()}
            onPressRight={handleBackCreateTicket}
          />
        </SafeAreaView>

        <BottomSheetCM
          isVisible={isVisible}
          title="Bộ lọc"
          onClose={() => setIsVisible(false)}
          renderContent={renderListFilter()}
        />
      </>
    </TouchableWithoutFeedback>
  );
};

export default AddTicketRelative;
