import {TouchableOpacity, View} from 'react-native';
import {Dropdown, MultiSelect} from 'react-native-element-dropdown';
import {FC, useState} from 'react';
import styles from './styles';
import {Color} from '../../../constants';
import TextCM from '../Text';
import {IconClear} from '../../../assets/images';

export interface IDropDown {
  label: string;
  value: any;
}

type Props = {
  placeholder?: string;
  dropdownPosition?: 'auto' | 'top' | 'bottom';
  maxHeight?: number;
  onSelect?: (item: IDropDown) => void;
  onSelectMui?: (item: IDropDown[]) => void;
  valueSelect?: IDropDown | undefined;
  valueMuiSelect?: IDropDown[];
  listData: IDropDown[];
  multiple?: boolean;
};

const DropdownListCM: FC<Props> = ({
  placeholder,
  dropdownPosition = 'auto',
  maxHeight = 340,
  onSelect,
  onSelectMui,
  valueSelect,
  listData,
  valueMuiSelect,
  multiple = false,
}) => {
  const [isFocus, setIsFocus] = useState(false);

  return (
    <View style={styles.container}>
      {!multiple ? (
        <Dropdown
          style={[styles.dropdown, isFocus && {borderColor: Color.borderColor}]}
          placeholderStyle={styles.placeholderStyle}
          selectedTextStyle={styles.selectedTextStyle}
          inputSearchStyle={styles.inputSearchStyle}
          iconStyle={styles.iconStyle}
          itemTextStyle={styles.itemTextStyle}
          containerStyle={styles.itemContainerStyle}
          data={listData}
          dropdownPosition={dropdownPosition}
          maxHeight={maxHeight}
          labelField="label"
          valueField="value"
          placeholder={!isFocus ? placeholder : ''}
          value={valueSelect?.value}
          onFocus={() => setIsFocus(true)}
          onBlur={() => setIsFocus(false)}
          onChange={item => {
            onSelect?.(item);
            setIsFocus(false);
          }}
        />
      ) : (
        <MultiSelect
          style={[styles.dropdown, isFocus && {borderColor: Color.borderColor}]}
          placeholderStyle={styles.placeholderStyle}
          selectedTextStyle={styles.selectedTextStyle}
          inputSearchStyle={styles.inputSearchStyle}
          iconStyle={styles.iconStyle}
          itemTextStyle={styles.itemTextStyle}
          containerStyle={styles.itemContainerStyle}
          data={listData}
          dropdownPosition={dropdownPosition}
          maxHeight={maxHeight}
          labelField="label"
          valueField="value"
          placeholder={!isFocus ? placeholder : ''}
          value={valueMuiSelect}
          onFocus={() => setIsFocus(true)}
          onBlur={() => setIsFocus(false)}
          onChange={item => {
            onSelectMui?.(item);
            setIsFocus(false);
          }}
          renderSelectedItem={(item, unSelect) => (
            <TouchableOpacity onPress={() => unSelect && unSelect(item)}>
              <View style={styles.selectedStyle}>
                <TextCM style={styles.textSelectedStyle}>{item.label}</TextCM>
                <IconClear />
              </View>
            </TouchableOpacity>
          )}
        />
      )}
    </View>
  );
};

export default DropdownListCM;
