import {isEmpty} from 'lodash';
import React, {FC, ReactElement, useRef, useState} from 'react';
import {
  FlatList,
  Modal,
  StyleProp,
  TouchableOpacity,
  View,
  ViewProps,
  ViewStyle,
} from 'react-native';
import TextCM from '../Text';
import {IconHozDot} from '../../../assets/images';
import {SizeDP} from '../../../constants/Size';
import styles from './styles';
import {Color} from '../../../constants';

export interface IDataDropdown {
  id: number;
  key: string;
  value: string;
  icon?: React.ReactNode;
}

type Props = {
  listDropdown: IDataDropdown[];
  onChangeItem: (item: IDataDropdown) => void;
  renderShowData?: React.ReactNode;
  dropDownStyle?: StyleProp<ViewStyle>;
};

const DropdownCM: FC<Props> = ({
  listDropdown,
  onChangeItem,
  renderShowData,
  dropDownStyle,
}) => {
  const [showModal, setShowModal] = useState<boolean>(false);

  const handleSelectItem = (item: IDataDropdown) => {
    onChangeItem(item);
    setShowModal(false);
  };

  const renderItem = ({
    item,
  }: {
    item: IDataDropdown;
  }): ReactElement<any, any> => (
    <TouchableOpacity
      style={styles.item}
      onPress={() => handleSelectItem(item)}>
      {!isEmpty(item.icon) && item.icon}
      {item.key && <TextCM>{item.key}</TextCM>}
    </TouchableOpacity>
  );

  return (
    <View style={{position: 'relative'}}>
      <TouchableOpacity
        style={{
          minWidth: SizeDP(20),
          alignItems: 'center',
          justifyContent: 'center',
        }}
        onPress={() => setShowModal(!showModal)}>
        {!isEmpty(renderShowData) ? renderShowData : <IconHozDot />}
      </TouchableOpacity>
      <Modal
        onDismiss={() => setShowModal(false)}
        visible={showModal}
        transparent
        animationType="fade"
        style={{backgroundColor: Color.BgPrimary, position: 'absolute'}}>
        <View style={[styles.dropdown, dropDownStyle]}>
          <FlatList
            data={listDropdown}
            renderItem={renderItem}
            keyExtractor={(item, index) => index.toString()}
          />
        </View>
      </Modal>
    </View>
  );
};

export default DropdownCM;
