import {TouchableOpacity, View} from 'react-native';
import {SafeAreaView} from 'react-native-safe-area-context';
import styles from './styles';
import {useNavigation} from '@react-navigation/native';
import {isUndefined} from 'lodash';
import {FC} from 'react';
import {IconArrowHeader, IconHozDot} from '../../../../../assets/images';
import {SizeDP} from '../../../../../constants/Size';
import TextCM from '../../../../components/Text';

type Props = {
  onPressIconLeft?: () => void;
};

const HeaderTicketDetail: FC<Props> = ({onPressIconLeft}) => {
  const navigation = useNavigation();

  const handleOnpressIconLeft = () => {
    if (isUndefined(onPressIconLeft)) {
      navigation.goBack();
    } else {
      onPressIconLeft();
    }
  };
  return (
    <View style={[styles.ctnMain]}>
      <SafeAreaView edges={['top']} style={styles.ctnHeader}>
        <View style={styles.ctnLeft}>
          <TouchableOpacity onPress={handleOnpressIconLeft}>
            <IconArrowHeader width={SizeDP(24)} height={SizeDP(24)} />
          </TouchableOpacity>
        </View>
        <TextCM style={[styles.txtTitle]}>{'Chi tiết giao dịch'}</TextCM>
        <TouchableOpacity hitSlop={SizeDP(15)} style={styles.right}>
          <IconHozDot />
        </TouchableOpacity>
      </SafeAreaView>
    </View>
  );
};

export default HeaderTicketDetail;
