//import liraries
import React, {useEffect, useState} from 'react';
import {View, FlatList} from 'react-native';
import {ListServices} from '../../../../../../../services';
import {CacheUtils} from '../../../../../../../utils';
import {ITicket} from '../../../../../../../model/interface/service.interface';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import CardCM from '../../../../../../components/Card';
import {IconEmpty} from '../../../../../../../assets/images';
import {SafeAreaView} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../../../../constants/Size';

// create a component
const TicketRelative = () => {
  const [listTicketRelative, setListTicketRelative] = useState<ITicket[]>([]);
  const [page, setPage] = useState<number>(0);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [totals, setTotals] = useState<number>(0);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {dataAccount} = useAppSelector(state => state.account);

  useEffect(() => {
    handleGetListTicketRelatives(page);
  }, [page]);

  const handleGetListTicketRelatives = async (page: number) => {
    try {
      const token = (await CacheUtils.getToken()) ?? '';
      const dataTicketRelative = await ListServices.getTicketRelatives(
        dataTicketRedux.id,
        page,
        token,
      );
      if (page === 0) {
        setListTicketRelative(dataTicketRelative.content);
      } else {
        setListTicketRelative([
          ...listTicketRelative,
          ...dataTicketRelative.content,
        ]);
      }
      setTotals(dataTicketRelative.totalElements);
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  const fetchNextPage = () => {
    if (listTicketRelative.length < totals) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setIsLoading(true);
    }
    setPage(0);
  };

  return (
    <SafeAreaView style={{height: SizeDP(500)}} edges={['bottom']}>
      {listTicketRelative.length > 0 ? (
        <FlatList
          nestedScrollEnabled={true}
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
          keyExtractor={item => item.id}
          onRefresh={onRefresh}
          refreshing={isLoading}
          onEndReachedThreshold={0.5}
          onEndReached={fetchNextPage}
          data={listTicketRelative}
          renderItem={({item}) => (
            <CardCM dataCard={item} account={dataAccount} isView={true} />
          )}
        />
      ) : (
        <View style={{alignItems: 'center', justifyContent: 'center', flex: 1}}>
          <IconEmpty />
        </View>
      )}
    </SafeAreaView>
  );
};

export default TicketRelative;
