import React from 'react';
import {useTranslation} from 'react-i18next';
import styles from './styles';
import {
  Keyboard,
  Modal,
  StyleProp,
  TouchableWithoutFeedback,
  View,
  ViewStyle,
} from 'react-native';
import {isEmpty} from 'lodash';
import ButtonCM from '../Button';
import TextCM from '../Text';
import {IconCancel, IconInfo, IconNotiModal} from '../../../assets/images';

type Props = {
  onClose: () => void;
  onConfirm?: () => void;
  isVisible: boolean;
  labelCancel?: string;
  labelConfirm?: string;
  labelClose?: string;
  title: string;
  content?: string;
  renderContent?: () => React.ReactNode;
  type?: 'delete' | 'success' | 'error' | 'info' | 'cancel' | 'noti';
  styleBtnLeft?: StyleProp<ViewStyle>;
  styleBtnRight?: StyleProp<ViewStyle>;
};

const ModalConfirmCM = ({
  onClose,
  onConfirm,
  isVisible,
  title,
  content,
  labelCancel,
  labelConfirm,
  renderContent,
  type = 'delete',
  styleBtnLeft,
  styleBtnRight,
}: Props) => {
  const {t} = useTranslation();

  const getIcon = () => {
    switch (type) {
      case 'cancel':
        return <IconCancel />;
      case 'delete':
        return <IconInfo />;
      case 'success':
        return <IconInfo />;
      case 'error':
        return <IconInfo />;
      case 'info':
        return <IconInfo />;
      case 'noti':
        return <IconNotiModal />;
    }
  };

  const onPressConfirm = () => {
    onConfirm?.();
  };

  const renderFooter = () => {
    return (
      <View style={styles.viewBtn}>
        <ButtonCM
          label={labelCancel}
          onPress={onClose}
          style={[styles.btn, styleBtnLeft, styles.btnLeft]}
          styleLabel={styles.labelStyleLeft}
        />
        <ButtonCM
          label={labelConfirm}
          onPress={onPressConfirm}
          style={[styles.btn, styleBtnRight]}
        />
      </View>
    );
  };

  return (
    <Modal visible={isVisible} transparent>
      <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
        <View style={styles.ctnContainer}>
          <View style={styles.container}>
            <View style={styles.body}>
              <View style={styles.viewIconDelete}>{getIcon()}</View>
              {!isEmpty(title) && (
                <TextCM style={styles.txtTitle}>{title}</TextCM>
              )}
              {!isEmpty(content) && (
                <TextCM style={styles.txtContent}>{content}</TextCM>
              )}
              {renderContent?.()}
            </View>
            {renderFooter()}
          </View>
        </View>
      </TouchableWithoutFeedback>
    </Modal>
  );
};

export default ModalConfirmCM;
