// Import external
import React, {FC} from 'react';
import {
  StyleProp,
  TextStyle,
  TouchableOpacity,
  View,
  ViewStyle,
} from 'react-native';

// Import internal

import {useNavigation} from '@react-navigation/native';
import {isEmpty, isUndefined} from 'lodash';
import {SafeAreaView} from 'react-native-safe-area-context';
import styles from './styles';
import {SizeDP} from '../../../constants/Size';
import {IconWhiteLeftArrow} from '../../../assets/images';
import TextCM from '../Text';

type Props = {
  style?: StyleProp<ViewStyle>;
  title?: string;
  onPressIconLeft?: () => void;
  viewIconLeft?: React.ReactNode;
  titleStyle?: StyleProp<TextStyle>;
  renderContentRight?: React.ReactNode;
};

const HeaderCM = ({
  style,
  onPressIconLeft,
  viewIconLeft,
  title = 'quangnh39',
  titleStyle,
  renderContentRight,
}: Props) => {
  const navigation = useNavigation();

  const handleOnpressIconLeft = () => {
    if (isUndefined(onPressIconLeft)) {
      navigation.goBack();
    } else {
      onPressIconLeft();
    }
  };
  return (
    <View style={[styles.ctnMain, style]}>
      <SafeAreaView edges={['top']} style={styles.ctnHeader}>
        <View style={styles.ctnLeft}>
          <TouchableOpacity
            hitSlop={SizeDP(15)}
            onPress={handleOnpressIconLeft}>
            {isEmpty(viewIconLeft) ? (
              <IconWhiteLeftArrow width={SizeDP(24)} height={SizeDP(24)} />
            ) : (
              viewIconLeft
            )}
          </TouchableOpacity>
          {!isEmpty(title) && (
            <TextCM style={[styles.txtTitle, titleStyle]}>{title}</TextCM>
          )}
        </View>
        {!isEmpty(renderContentRight) && renderContentRight}
      </SafeAreaView>
    </View>
  );
};

export default HeaderCM;
