import {LayoutChangeEvent, TouchableOpacity, View} from 'react-native';
import TextCM from '../../../../components/Text';
import styles from './styles';
import {
  IconClock,
  IconFilePresent,
  IconHozDot,
  IconMessageChat,
} from '../../../../../assets/images';
import {SizeDP} from '../../../../../constants/Size';
import {FC, useState} from 'react';
import {ITicket} from '../../../../../model/interface/service.interface';
import {DateUtils} from '../../../../../utils';
import {isEmpty, isNull} from 'lodash';
import {useNavigation} from '@react-navigation/native';
import {NavigationName} from '../../../../../constants';
import {useTranslation} from 'react-i18next';

type Props = {
  item: ITicket;
  colorStatus: string;
  bgColorStatus: string;
};

const KanBanItem: FC<Props> = ({item, colorStatus, bgColorStatus}) => {
  const navigation = useNavigation<any>();
  const {t} = useTranslation();
  const [widthOfItem, setWidthOfItem] = useState<number>(0);
  const onLayout = (event: LayoutChangeEvent) => {
    const {x, y, height, width} = event.nativeEvent.layout;
    setWidthOfItem(width);
  };

  const handleDirectTicketDetail = () => {
    navigation.navigate(NavigationName.BottomTabNavigationTicketDetail, {
      dataTicket: item,
      colorStatus: colorStatus,
      bgColorStatus: bgColorStatus,
      selectTabBottom: 1,
    });
  };

  const getTitle = () => {
    return isEmpty(item.title) ? '---' : item.title;
  };

  return (
    <TouchableOpacity
      onPress={handleDirectTicketDetail}
      onLayout={onLayout}
      style={styles.ctnMain}>
      <View style={styles.ctnHeader}>
        <TextCM style={[styles.title, {width: widthOfItem - SizeDP(100)}]}>
          {getTitle()}
        </TextCM>
        <View style={styles.time}>
          {!isNull(item.delayTime) && (
            <IconClock width={SizeDP(16)} height={SizeDP(16)} />
          )}
          <TouchableOpacity style={styles.center}>
            <IconHozDot />
          </TouchableOpacity>
        </View>
      </View>
      <TextCM>
        Ngày tạo: {DateUtils.convertDateToDDMMYYYY_H_M(item.createdDate)}
      </TextCM>
      <View style={styles.ctnFooter}>
        <View style={[styles.rowCenter]}>
          <View style={[styles.viewId]}>
            <TextCM
              numberOfLines={1}
              style={{width: SizeDP(50)}}>{`#${item.id}`}</TextCM>
          </View>
          <View style={[styles.viewStatus, {backgroundColor: bgColorStatus}]}>
            <TextCM style={[{color: colorStatus}]}>
              {t(`TK.${item.ticketStatus}`)}
            </TextCM>
          </View>
        </View>
        <View style={[styles.rowCenter]}>
          <View style={[styles.rowCenter]}>
            <IconFilePresent width={SizeDP(20)} height={SizeDP(20)} />
            <TextCM>{item.attachmentCount}</TextCM>
          </View>
          <View style={[styles.rowCenter]}>
            <IconMessageChat width={SizeDP(20)} height={SizeDP(20)} />
            <TextCM>{item.commentCount}</TextCM>
          </View>
        </View>
      </View>
    </TouchableOpacity>
  );
};

export default KanBanItem;
