//import liraries
import React, {useEffect, useState} from 'react';
import {FlatList, View} from 'react-native';
import styles from './styles';
import TextCM from '../../../../../../components/Text';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {CacheUtils, DateUtils} from '../../../../../../../utils';
import {ListServices} from '../../../../../../../services';
import {SafeAreaView} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../../../../constants/Size';

export const HISTORY_ACTION = {
  CREATE: 'add',
  PUBLIC: 'public',
  UNPUBLIC: 'unpublic',
  ACTIVE: 'active',
  INACTIVE: 'inactive',
  UPDATE: 'edit',
  DELETE: 'delete',
  ASSIGN: 'assign',
  REQUEST_APP: 'request_approve',
  COMMENT: 'comment',
  REJECT: 'reject_approve',
  REJECT_CONSIDER: 'reject_consider',
  AUTHORITY: 'authority',
  REQUEST_UPDATE: 'request_update',
  SIGNING: 'signing',
  UPLOAD: 'upload',
  CANCEL: 'cancel',
  EDIT_COMMENT: 'edit_comment',
  REJECT_APPROVE: 'reject_approve',
  REVIEW_CONDUCT: 'review_conduct',
};

interface IHistories {
  id: string;
  ticketId: string;
  userTaskId: null;
  notionName: null;
  action: 'add';
  entity: 'ticket';
  createdDate: string;
  createdBy: string;
  lastModifiedDate: string;
  lastModifiedBy: string;
  custId: number;
  orgIn: string;
  name: null;
  avatar: null;
}

// create a component
const HistoryTicket = () => {
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const [page, setPage] = useState<number>(0);
  const [totals, setTotals] = useState<number>(0);
  const [refreshing, setRefreshing] = useState<boolean>(false);
  const [listHistories, setListHistories] = useState<IHistories[]>([]);

  useEffect(() => {
    (async () => {
      await getTicketHistories(page);
    })();
  }, [page]);

  const getTicketHistories = async (page: number) => {
    try {
      const token = (await CacheUtils.getToken()) ?? '';
      const dataTicketHistory = await ListServices.getTicketHistories(
        page,
        dataTicketRedux.id,
        token,
      );
      if (page === 0) {
        setListHistories(dataTicketHistory?.data);
      } else {
        setListHistories([...listHistories, ...dataTicketHistory?.data]);
      }
      setTotals(dataTicketHistory!.totalRecords);
    } catch (error) {
      console.error(error);
    }
  };

  const fetchNextPage = () => {
    if (listHistories.length < totals) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setRefreshing(true);
    }
    setPage(0);
  };

  const renderItem = ({item, index}: {item: IHistories; index: number}) => {
    return (
      <>
        <View style={styles.ctnItem}>
          <View style={styles.ctnAction}></View>
          <View style={{rowGap: SizeDP(4)}}>
            <TextCM style={styles.title}>{item.action}</TextCM>
            <View style={styles.ctnDes}>
              <TextCM style={styles.Des}>
                {`${DateUtils.convertDateToDDMMYYYY_H_M(
                  item.createdDate,
                )} bởi ${item.createdBy}`}
              </TextCM>
            </View>
          </View>
        </View>
        {listHistories.length - 1 !== index && (
          <View style={styles.tabHoz}></View>
        )}
      </>
    );
  };

  return (
    <SafeAreaView style={styles.container} edges={['bottom']}>
      {listHistories.length > 0 && (
        <FlatList
          keyExtractor={item => item.id}
          refreshing={refreshing}
          onRefresh={onRefresh}
          data={listHistories}
          onEndReached={fetchNextPage}
          onEndReachedThreshold={0.5}
          renderItem={renderItem}
        />
      )}
    </SafeAreaView>
  );
};

//make this component available to the app
export default HistoryTicket;
