import {
  BottomTabBarProps,
  createBottomTabNavigator,
} from '@react-navigation/bottom-tabs';
import {TouchableOpacity, View} from 'react-native';
import {SafeAreaView} from 'react-native-safe-area-context';
import styles from './styles';
import {SizeDP} from '../../constants/Size';
import i18n from '../../i18n';
import {
  IconFollowTicket,
  IconFollowTicketSelect,
  IconHome,
  IconHomeSelect,
  IconMyTicket,
  IconMyTicketSelect,
  IconNoti,
  IconNotiSelect,
} from '../../assets/images';
import TextCM from '../../app/components/Text';
import TicketDetail from '../../app/screens/ticket-detail';
import {useRoute} from '@react-navigation/native';
import CommunicationTicketScreen from '../../app/screens/ticket-detail/communication-ticket';
import RiskTicketScreen from '../../app/screens/ticket-detail/risk-ticket';

export const TabName = {
  HomeX: 'HomeX',
  Communication: 'Communication',
  Risk: 'Risk',
};

const Tabs = createBottomTabNavigator();

const MyTabBar = ({state, navigation}: BottomTabBarProps) => {
  return (
    <SafeAreaView
      edges={['bottom', 'left', 'right']}
      style={styles.ctnSafeTabBottom}>
      <View style={styles.ctnTabBottom}>
        {state.routes.map((route, index) => {
          let label = '';
          const isFocused = state.index === index;
          function getIcon() {
            switch (route.name) {
              case TabName.HomeX:
                label = i18n.t('Trang chủ');
                return (
                  <>
                    {isFocused ? (
                      <IconHomeSelect width={SizeDP(23)} height={SizeDP(20)} />
                    ) : (
                      <IconHome width={SizeDP(23)} height={SizeDP(20)} />
                    )}
                  </>
                );
                label = i18n.t('Công việc');
                return (
                  <>
                    {isFocused ? (
                      <IconMyTicketSelect
                        width={SizeDP(23)}
                        height={SizeDP(20)}
                      />
                    ) : (
                      <IconMyTicket width={SizeDP(23)} height={SizeDP(20)} />
                    )}
                  </>
                );
              case TabName.Communication:
                label = i18n.t('Thảo luận');
                return (
                  <>
                    {isFocused ? (
                      <IconFollowTicketSelect
                        width={SizeDP(23)}
                        height={SizeDP(20)}
                      />
                    ) : (
                      <IconFollowTicket
                        width={SizeDP(23)}
                        height={SizeDP(20)}
                      />
                    )}
                  </>
                );
              case TabName.Risk:
                label = i18n.t('Rủi ro');
                return (
                  <>
                    {isFocused ? (
                      <IconNotiSelect width={SizeDP(23)} height={SizeDP(20)} />
                    ) : (
                      <IconNoti width={SizeDP(23)} height={SizeDP(20)} />
                    )}
                  </>
                );

              default:
                break;
            }
          }

          const onPress = () => {
            const event = navigation.emit({
              type: 'tabPress',
              target: route.key,
              canPreventDefault: true,
            });

            if (!isFocused && !event.defaultPrevented) {
              navigation.navigate(route.name, route.params);
            }
          };

          return (
            <TouchableOpacity
              accessibilityRole="button"
              style={styles.ctnItemTabBottom}
              accessibilityState={isFocused ? {selected: true} : {}}
              key={index}
              onPress={onPress}>
              {getIcon()}
              <TextCM
                style={isFocused ? styles.txtLabelSelect : styles.txtLabel}>
                {label}
              </TextCM>
            </TouchableOpacity>
          );
        })}
      </View>
    </SafeAreaView>
  );
};
const BottomTabNavigationTicketDetail = () => {
  const route = useRoute();

  return (
    <Tabs.Navigator tabBar={MyTabBar} screenOptions={{headerShown: false}}>
      <Tabs.Screen
        name={TabName.HomeX}
        component={TicketDetail}
        initialParams={route.params}
      />
      <Tabs.Screen
        name={TabName.Communication}
        component={CommunicationTicketScreen}
        initialParams={route.params}
      />
      <Tabs.Screen
        name={TabName.Risk}
        component={RiskTicketScreen}
        initialParams={route.params}
      />
    </Tabs.Navigator>
  );
};

export default BottomTabNavigationTicketDetail;
