//import liraries
import React, {useCallback, useEffect, useState} from 'react';
import {FlatList, Image, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import TextCM from '../../components/Text';
import {IconDefault, IconEmpty} from '../../../assets/images';
import {SizeDP} from '../../../constants/Size';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {ListServices} from '../../../services';
import {IServices} from '../../../model/interface/service.interface';
import {isEmpty, isNull, isUndefined} from 'lodash';
import {Color, NavigationName, String} from '../../../constants';
import {CacheUtils} from '../../../utils';
import HeaderCM from '../../components/Header/HeaderCM';
import {useAppDispatch} from '../../../hooks/useRedux';
import {commonActions} from '../../../redux/slices';

// create a component
const Home = () => {
  const navigation = useNavigation<any>();
  const dispatch = useAppDispatch();
  const [listServices, setListServices] = useState<IServices[]>();

  useFocusEffect(
    useCallback(() => {
      (async () => {
        try {
          // const datax = await ListServices.login();
          // await CacheUtils.setToken(datax.access_token);
          // dispatch(commonActions.setAccessToken(datax.access_token));
          const token = (await CacheUtils.getToken()) ?? '';
          console.log('token', token);

          const data = await ListServices.getListServives(token);
          setListServices(data.content);
        } catch (error) {}
      })();
    }, []),
  );

  const handleServiceDetail = (item: IServices) => {
    navigation.navigate(NavigationName.ServiceDetail, item);
  };

  const renderListServiceItem = ({item}: {item: IServices}) => {
    return (
      <TouchableOpacity
        onPress={() => handleServiceDetail(item)}
        style={styles.ctnItemList}>
        <View
          style={[
            styles.ctnImage,
            {
              backgroundColor: !isNull(item.color)
                ? item.color
                : Color.BgPrimary,
            },
          ]}>
          {isNull(item.iconBase64) ? (
            <IconDefault />
          ) : (
            <Image
              source={{
                uri: `${String.PREFIX_IMG_BASE64}${item.iconBase64}`,
              }}
            />
          )}
        </View>
        <View style={{flex: 1, rowGap: SizeDP(8)}}>
          <TextCM style={styles.textTitleItem}>{item.serviceName}</TextCM>
          <TextCM style={styles.textDes}>{item.description}</TextCM>
        </View>
      </TouchableOpacity>
    );
  };

  return (
    <>
      <HeaderCM title="ERequest" />
      <View style={styles.ctnMain}>
        <TextCM style={styles.textTitle}>Danh sách dịch vụ</TextCM>

        {!isUndefined(listServices) && listServices.length > 0 ? (
          <FlatList
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            data={listServices}
            renderItem={renderListServiceItem}
          />
        ) : (
          <View style={styles.viewListServices}>
            <IconEmpty />
            <TextCM style={styles.textH1}>Hệ thống chưa có dịch vụ nào!</TextCM>
            <TextCM>Danh sách các dịch vụ sẽ được hiển thị tại đây</TextCM>
          </View>
        )}
      </View>
    </>
  );
};

//make this component available to the app
export default Home;
