import {useRoute} from '@react-navigation/native';
import {ITicket} from '../../../model/interface/service.interface';
import {
  FlatList,
  Image,
  LayoutChangeEvent,
  ScrollView,
  StatusBar,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import TextCM from '../../components/Text';
import {SizeDP} from '../../../constants/Size';
import {useCallback, useEffect, useState} from 'react';
import {
  IconArrowHeader,
  IconDocument,
  IconMailFill,
  IconPeople,
  IconViewOn,
  Iconhigh,
  Iconlow,
  Iconmedium,
} from '../../../assets/images';
import HeaderTicketDetail from './components/Header';
import TabViewCM from './components/TabView';
import {ListServices} from '../../../services';
import {CacheUtils} from '../../../utils';
import {Color, String} from '../../../constants';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import {ticketActions} from '../../../redux/slices';
import OrginServices from '../../../services/orgin.services';
import {SafeAreaView} from 'react-native-safe-area-context';
import {Propity} from '../../../model/enum/common.enum';
import {useTranslation} from 'react-i18next';

interface IRoute {
  dataTicket: ITicket;
  colorStatus: string;
  bgColorStatus: string;
  selectTabBottom: number;
}

const TicketDetail = () => {
  const route = useRoute<any>();
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const {dataTicket, colorStatus, bgColorStatus, selectTabBottom}: IRoute =
    route?.params;
  const [widthLayout, setWidthLayout] = useState<number>(0);
  const [isShow, setIsShow] = useState<boolean>(false);

  const {dataTicketRedux, dataUserTicket, dataOrgin} = useAppSelector(
    state => state.ticket,
  );
  const {dataAccount} = useAppSelector(state => state.account);

  useEffect(() => {
    (async () => {
      await getAccountUserTicket();
    })();
  }, [selectTabBottom]);

  const getAccountUserTicket = async () => {
    try {
      const token = (await CacheUtils.getToken()) ?? '';
      const ticketId =
        selectTabBottom === 1
          ? dataTicket.id
          : dataTicket?.resourceId?.substr(4);
      const email =
        selectTabBottom === 1
          ? dataTicket.createdBy
          : (dataTicket.owner as any);
      Promise.all([
        ListServices.getTicketById(ticketId, token),
        ListServices.getMultiLoginAccount(email, token),
      ])
        .then(async values => {
          const orgIn = values[0]?.orgIn?.split('/')[2] as any;
          const dataOrg = await OrginServices.getOrigin(orgIn, token);
          dispatch(ticketActions.setDataTicket(values[0]));
          dispatch(ticketActions.setDataUserTicket(values[1][0]));
          dispatch(ticketActions.setDataOrgin(dataOrg));
        })
        .catch(err => {
          console.log(err);
        });
    } catch (error) {
      console.log(error);
    }
  };

  const renderPriority = (priority: Propity) => {
    switch (priority) {
      case Propity.high:
        return <Iconhigh width={SizeDP(20)} height={SizeDP(20)} />;
      case Propity.medium:
        return <Iconmedium width={SizeDP(20)} height={SizeDP(20)} />;
      case Propity.low:
        return <Iconlow width={SizeDP(16)} height={SizeDP(16)} />;
    }
  };

  const onLayout = useCallback((event: LayoutChangeEvent) => {
    const {width} = event.nativeEvent.layout;
    setWidthLayout(width);
  }, []);

  return (
    <>
      <StatusBar
        barStyle={'dark-content'}
        backgroundColor={Color.Transparent}
      />
      <HeaderTicketDetail />
      {dataTicketRedux && dataUserTicket && (
        <View style={{backgroundColor: '#fff'}}>
          <View style={[styles.ctnbodyDetail]}>
            <View
              style={{
                paddingHorizontal: SizeDP(16),
                position: 'relative',
              }}>
              <TextCM style={styles.textTitle}>
                {dataTicketRedux.title ? dataTicketRedux.title : '---'}
              </TextCM>

              <TouchableOpacity
                onPress={() => setIsShow(!isShow)}
                hitSlop={SizeDP(15)}
                style={{
                  position: 'absolute',
                  transform: [{rotate: isShow ? '90deg' : '-90deg'}],
                  right: SizeDP(16),
                  top: 0,
                  zIndex: 2,
                }}>
                <IconArrowHeader />
              </TouchableOpacity>
            </View>
            <View style={{alignItems: 'center', justifyContent: 'center'}}>
              <View
                style={[styles.ctnStatus, {backgroundColor: bgColorStatus}]}>
                <TextCM style={[styles.textDes, {color: colorStatus}]}>
                  {t(`TK.${dataTicketRedux.ticketStatus}`)}
                </TextCM>
              </View>
            </View>

            {isShow && (
              <>
                <View onLayout={onLayout} style={styles.rowCenterStart}>
                  <View
                    style={[
                      styles.columnCenter,
                      {width: (widthLayout - SizeDP(32)) / 3},
                    ]}>
                    <View style={styles.ctnImage}>
                      <Image
                        source={{
                          uri:
                            String.PREFIX_IMG_BASE64 + dataUserTicket?.avatar,
                        }}
                      />
                    </View>
                    <TextCM style={styles.textTit}>Nhân viên</TextCM>
                    <TextCM style={styles.textDes}>
                      {'' +
                        dataUserTicket?.firstName +
                        dataUserTicket?.lastName}
                    </TextCM>
                  </View>
                  <View style={styles.borderStyle}></View>
                  <View
                    style={[
                      styles.columnCenter,
                      {width: (widthLayout - SizeDP(32)) / 3},
                    ]}>
                    <IconMailFill />
                    <TextCM style={styles.textTit}>Email</TextCM>
                    <TextCM style={styles.textDes}>
                      {dataUserTicket?.email}
                    </TextCM>
                  </View>
                  <View style={styles.borderStyle}></View>
                  <View
                    style={[
                      styles.columnCenter,
                      {width: (widthLayout - SizeDP(32)) / 3},
                    ]}>
                    <IconPeople />
                    <TextCM style={styles.textTit}>Tổ chức</TextCM>
                    <TextCM style={styles.textDes}>{dataOrgin?.name}</TextCM>
                  </View>
                </View>
              </>
            )}
          </View>
          {dataTicketRedux?.formData?.[0]?.version && <TabViewCM />}
        </View>
      )}
    </>
  );
};
export default TicketDetail;
