import {IPayloadSearchTicket} from '../model/interface/service.interface';
import {CacheUtils} from '../utils';
import {BASE_URL} from './httpClient';

export default class ListServices {
  static async login() {
    const endPoint = '/auth/login';
    try {
      const data = await fetch(
        'https://demo.eaccount.kyta.fpt.com' + endPoint,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            username: 'elmstest7@gmail.com',
            password: 'admin@123',
          }),
        },
      );
      return Promise.resolve(data.json());
    } catch (error) {
      return Promise.reject(error);
    }
  }
  // [POST] /services/uaa/api/p/customer-logins/email | Get info about email
  static async getListServives(accessToken: string) {
    const endPoint =
      '/services/erequest/api/services/search?sort=displayOrder,asc&page=0&size=10000&sort=createdDate,desc';
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify({
          isAdmin: false,
          status: ['display'],
        }),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getByServiceId(serviceId: string, accessToken: string) {
    const endPoint = `/services/erequest/api/work-flows/get-by-serviceId?serviceId=${serviceId}`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getModeler(modelerId: string, accessToken: string) {
    const endPoint = `/services/idaas-eflow/api/modelers/${modelerId}`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async createTicket(dataBody: any, accessToken: string) {
    const endPoint = `/services/erequest/api/tickets`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(dataBody),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getTicketById(ticketId: any, accessToken: string) {
    const endPoint = `/services/erequest/api/tickets/${ticketId}`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getServiceMenu(
    page: number,
    size: number,
    textSearch: string | undefined,
    accessToken: string,
    checkFilter?: boolean,
  ) {
    const endpoint = `/services/erequest/api/services/search?sort=displayOrder,asc&page=${page}&size=${size}&sort=createdDate,desc`;

    let body: any = {
      status: checkFilter ? [] : ['display'],
      isAdmin: false,
    };

    if (textSearch) {
      body = {
        ...body,
        textSearch: textSearch,
      };
    }

    try {
      const data = await fetch(BASE_URL + endpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(body),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      return Promise.reject(error);
    }
  }

  static async getTicketList(
    page: number,
    size: number,
    sort: string,
    accessToken: string,
    payload?: any,
  ) {
    const endpoint = `/services/erequest/api/tickets/search?page=${page}&size=${size}&sort=${sort}`;

    try {
      const data = await fetch(BASE_URL + endpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(payload),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      return Promise.reject(error);
    }
  }

  static async getTicketListFollow(
    page: number,
    size: number,
    sort: string,
    search: any,
    accessToken: string,
  ) {
    const params = new URLSearchParams({
      search: JSON.stringify(search),
    });
    const endpoint = `/services/workboard/api/notifications/tasks/all?page=${page}&size=${size}&sort=${sort}&${params}`;
    try {
      const data = await fetch(BASE_URL + endpoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve({
        content: await data.json(),
        page: {
          totalRecords: data.headers.get('x-total-count'),
        },
      });
    } catch (error) {
      console.log(error);
      return Promise.reject(error);
    }
  }

  static async getTicketHistories(
    page: number,
    ticketId: string,
    accessToken: string,
  ) {
    const endPoint = `/services/erequest/api/histories/get-by-ticketId?ticketId=${ticketId}&sort=createdDate,desc&page=${page}&size=20`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve({
        data: await data.json(),
        totalRecords: Number(data.headers.get('x-total-count')),
      });
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getMultiLoginAccount(email: string, accessToken: string) {
    const endPoint = '/services/uaa/api/p/users/multi-login';
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify({
          data: [email],
        }),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getTicketRelatives(
    ticketId: string,
    page: number,
    accessToken: string,
  ) {
    const endPoint = `/services/erequest/api/relatives/get-by-ticketId?ticketId=${ticketId}&page=${page}&size=20`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }
}
